.header {
  background-image: url("././../images/bg-pattern.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 1.25rem;
  height: 350px;
  text-align: center;
}
.header__descript {
  background-image: url("././../images/pattern-circles.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding: 1.25rem;
}
.header__descript h1 {
  padding-bottom: 0.625rem;
  font-size: 1.25rem;
  font-weight: 800;
  color: var(--darkDesaturatedBlue-TextandCTA-Background);
}
@media (min-width: 71.875em) {
  .header__descript h1 {
    font-size: 1.875rem;
  }
}
.header__descript p {
  font-size: 0.9375rem;
  color: var(--grayishBlue-Text);
  max-width: 21ch;
  line-height: 1.6;
  margin-inline: auto;
}
@media (min-width: 71.875em) {
  .header__descript p {
    max-width: none;
  }
}

.main {
  width: 90%;
  margin: auto;
  margin-block-start: -8.75rem;
  background-color: var(--white-PricingComponentBackground);
  border-radius: 0.625rem;
  text-align: center;
  box-shadow: 0px 12px 23px -2px rgba(0, 0, 0, 0.4);
}
@media (min-width: 71.875em) {
  .main {
    width: 50%;
    margin-block-start: -10.625rem;
    display: grid;
    grid-template-areas: "view view money money" "slider slider slider slider" "toggle toggle toggle toggle" "offering offering offering offering";
  }
}
.main__pageviews {
  padding: 2.5rem 1.25rem 1.25rem 1.25rem;
  color: var(--grayishBlue-Text);
  text-transform: uppercase;
  font-size: 0.9375rem;
  word-spacing: 0.3125rem;
  font-weight: 800;
}
@media (min-width: 71.875em) {
  .main__pageviews {
    grid-area: view;
    justify-self: start;
    align-self: center;
    padding: 2.5rem;
  }
}
@media (min-width: 71.875em) {
  .main__slider {
    grid-area: slider;
    width: 90%;
    align-self: center;
    justify-self: center;
  }
}
.main__month {
  padding: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 71.875em) {
  .main__month {
    grid-area: money;
    align-self: center;
    justify-self: end;
    padding: 2.5rem;
  }
}
.main__month h2 {
  font-size: 1.875rem;
  font-weight: 800;
  padding-right: 0.3125rem;
  color: var(--darkDesaturatedBlue-TextandCTA-Background);
}
.main__month p {
  color: var(--grayishBlue-Text);
  font-size: 0.9375rem;
}
@media (min-width: 71.875em) {
  .main__toggle {
    grid-area: toggle;
    align-self: center;
    justify-self: center;
    padding: 1.25rem;
    margin-left: 6.25rem;
  }
}
.main__offering {
  border-top: 1px solid var(--grayishBlue-Text);
  padding: 1.25rem;
  color: var(--grayishBlue-Text);
}
@media (min-width: 71.875em) {
  .main__offering {
    grid-area: offering;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.875rem;
  }
}
.main__offering ul {
  list-style-image: url("././../images/icon-check.svg");
  list-style-position: inside;
}
@media (min-width: 71.875em) {
  .main__offering ul {
    text-align: start;
  }
}
.main__offering ul li {
  padding: 0.3125rem 0 0.3125rem 0;
}
.main__offering .button {
  width: 60%;
  padding: 0.9375rem;
  margin-top: 0.625rem;
  color: var(--paleBlue-CTA-Text);
  border: none;
  background-color: var(--darkDesaturatedBlue-TextandCTA-Background);
  border-radius: 1.25rem;
  font-size: 0.9375rem;
  font-weight: 600;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .main__offering .button {
    width: 30%;
  }
}
.main__offering .button:hover, .main__offering .button:active {
  color: var(--white-PricingComponentBackground);
}

.toggle {
  display: flex;
  justify-content: center;
  align-items: baseline;
  border: none;
  color: var(--grayishBlue-Text);
  align-items: center;
  padding-block: 1.25rem;
}
.toggle label {
  cursor: pointer;
}
.toggle label:first-of-type {
  font-size: 0.9375rem;
  padding-right: 0.625rem;
}
.toggle label:last-of-type {
  grid-area: year;
  font-size: 0.9375rem;
  padding: 0 0.625rem 0 0.9375rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5625rem;
}
.toggle__wrapper input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.75rem;
  height: 1.75rem;
  cursor: pointer;
}
.toggle__background {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  pointer-events: none;
  background-color: var(--lightGrayishBlue-ToggleBackground);
}
.toggle .active {
  background-color: var(--strongCyan-SliderBackround);
}
.toggle__button {
  position: absolute;
  left: 0.25rem;
  top: 0.25rem;
  right: 100%;
  width: 1.0625rem;
  height: 1.0625rem;
  border-radius: 50%;
  background-color: var(--white-PricingComponentBackground);
  transition: all 150ms ease-in-out;
}
.toggle #yearlyB:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle p {
  grid-area: minus;
  font-size: 0.875rem;
  background: var(--lightGrayishRed-DiscountBackground);
  color: var(--lightRed-DiscountText);
  border-radius: 1.25rem;
  padding: 0.3125rem;
}
@media (min-width: 71.875em) {
  .toggle p {
    font-size: 0.9375rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.toggle p span {
  display: none;
}
@media (min-width: 71.875em) {
  .toggle p span {
    display: block;
    padding-left: 0.625rem;
  }
}

.range {
  width: 90%;
  margin: auto;
  position: relative;
}
.range input[type=range] {
  appearance: none;
  width: 100%;
  height: 0.9375rem;
  outline: none;
  border-radius: 1.25rem;
  background-color: var(--lightGrayishBlue-EmptySliderBar);
  cursor: pointer;
}
.range input[type=range]::-webkit-slider-thumb {
  appearance: none;
  width: 2.25rem;
  height: 2.25rem;
  cursor: pointer;
  z-index: 3;
  position: relative;
  border-radius: 50%;
  background-color: var(--strongCyan-SliderBackround);
  background-image: url("././../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.range input[type=range]::-webkit-slider-thumb:hover {
  box-shadow: 0px 5px 13px 3px var(--strongCyan-SliderBackround-active);
  transition: 0.3s ease;
  cursor: pointer;
}
.range input[type=range]::-webkit-slider-thumb:active {
  background-color: var(--strongCyan-SliderBackround-active);
  box-shadow: 0px 0px 15px 5px var(--strongCyan-SliderBackround);
  cursor: grabbing;
}
.range input[type=range]::-moz-range-thumb {
  appearance: none;
  width: 2.25rem;
  height: 2.25rem;
  cursor: pointer;
  z-index: 3;
  position: relative;
  border-radius: 50%;
  border: none;
  background-color: var(--strongCyan-SliderBackround);
  background-image: url("././../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.range input[type=range]::-moz-range-thumb:hover {
  box-shadow: 0px 5px 13px 3px var(--strongCyan-SliderBackround-active);
  transition: 0.3s ease;
  cursor: pointer;
}
.range input[type=range]::-moz-range-thumb:active {
  background-color: var(--strongCyan-SliderBackround-active);
  box-shadow: 0px 0px 15px 5px var(--strongCyan-SliderBackround);
  cursor: grabbing;
}
.range__selector {
  width: 2.5rem;
  height: 2.25rem;
  position: absolute;
  bottom: -0.3125rem;
  left: 50%;
  transform: translateX(-50%);
}
.range__background {
  width: 50%;
  height: 0.9375rem;
  background-color: var(--strongCyan-SliderBackround);
  border-radius: 1.25rem;
  position: absolute;
  top: 1px;
  left: 0;
  pointer-events: none;
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}

:root {
  /* Primary */
  --softCyan-FullSliderBar: hsl(174, 77%, 80%);
  --strongCyan-SliderBackround: hsl(174, 86%, 45%);
  --strongCyan-SliderBackround-active: hsl(174, 86%, 35%);
  --lightGrayishRed-DiscountBackground: hsl(14, 92%, 95%);
  --lightRed-DiscountText: hsl(15, 100%, 70%);
  --paleBlue-CTA-Text: hsl(226, 100%, 87%);
  /* Neutral */
  --white-PricingComponentBackground: hsl(0, 0%, 100%);
  --veryPaleBlue-MainBackground: hsl(230, 100%, 99%);
  --lightGrayishBlue-EmptySliderBar: hsl(224, 65%, 95%);
  --lightGrayishBlue-ToggleBackground: hsl(223, 50%, 87%);
  --grayishBlue-Text: hsl(225, 20%, 60%);
  --darkDesaturatedBlue-TextandCTA-Background: hsl(227, 35%, 25%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background-color: var(--veryPaleBlue-MainBackground);
}